import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { UseFormSetValue } from 'react-hook-form/dist/types/form';
import { Dispatcher } from '../../../../types/store';
import { OrderStatusForm, OrderStatusState } from '../../../../types/OrderStatus';
import { fetchOrderStatus } from '../../../../store/OrderStatus/thunks';
import { clearUploadedOrderStatusData } from '../../../../store/OrderStatus/actions';
import { useNavigate } from 'react-router-dom';

export const useOrderStatusById = (orderStatusId: string, setValue: UseFormSetValue<OrderStatusForm>): boolean => {
  const dispatch = useDispatch<Dispatcher>();
  const { orderStatusById, orderStatusByIdLoading, orderStatusByIdError } = useSelector<{
    orderStatus: OrderStatusState;
  }, OrderStatusState>((state) => state.orderStatus);

  const setValuesToForm = async (orderStatus): Promise<void> => {
    setValue('name', orderStatus.name, { shouldDirty: true });
    console.log(orderStatus);
  };

  useEffect(() => {
    if (orderStatusId) {
      dispatch(fetchOrderStatus(orderStatusId));
    }
  }, [orderStatusId]);

  useEffect(() => {
    if (orderStatusById && orderStatusId) {
      setValuesToForm(orderStatusById);
    }
  }, [orderStatusById]);

  return orderStatusByIdLoading;
};

interface IUseUpload {
  entityId: string | null,
  uploadedEntity: object,
  path: string,
}

export const useUploadedEntity: IUseUpload = (entityId, uploadedEntity, path): void => {
  const navigate = useNavigate();
  const dispatch = useDispatch<Dispatcher>();
  useEffect(() => {
    if (uploadedEntity && !entityId) {
      navigate(`/edit/${path}`);
      dispatch(clearUploadedOrderStatusData());
    }
  }, [uploadedEntity]);
};
